/* Reset and base styles  */

* {
    padding: 0;
    margin: 0;
    border: none;
    outline: none !important;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --bs-body-color: #adb5bd;
    --px-theme: #004a77;
    --px-theme-text: #c2e7ff;
    --px-bg: #1f1f1f;
    --px-text: #c4c7c5;
    --px-gray-1: #28292a;
    --px-gray-2: #2d2f31;
    --px-gray-3: #333438;
    --px-gray-4: #34363a;
    --px-gray-5: #383a3e;
    --bs-heading-color: #e3e3e3;
    --px-border: #444746;
    --bs-border-width: 1px;
    --bs-border-color: #495057;
}

body {
    background-color: var(--px-bg);
    color: var(--px-text);
    font-family: Rubik, sans-serif;
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.6;
    overflow-x: hidden;
}

section {
    display: block;
}

li, a {
    list-style: none;
    text-decoration: none;
}

img {
    max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: 0.5rem;
    font-weight: 500;
    line-height: 1.2;
    color: var(--bs-heading-color, inherit);
}

p {
    margin-top: 0;
    margin-bottom: 1rem;
}

label {
    display: inline-block;
}

button, input, optgroup, select, textarea {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}
button {
    cursor: pointer;
}

textarea {
    resize: vertical;
}